import React, { useEffect } from 'react';
import BasedPost from 'gatsby-theme-blog/src/components/post';
import Helmet from 'react-helmet';
import Gitalk from 'gitalk';
import 'gitalk/dist/gitalk.css';

const options = {
  weekday: 'long',
  year: 'numeric',
  month: 'long',
  day: 'numeric',
};

const Post = (props) => {
  const getComment = () => {
    setTimeout(() => {
      const gitalk = new Gitalk({
        clientID: 'fbd1c7dd30228f2387a4',
        clientSecret: '06c0e4f34e00345b40c2e48283f34dc9829395c7',
        repo: 'vietanhlehuu.github.io',
        owner: 'vietanhlehuu',
        admin: ['vietanhlehuu', 'anhhuuvietle'],
        id: window.location.pathname, // Ensure uniqueness and length less than 50
        distractionFreeMode: false, // Facebook-like distraction free mode
      });
      gitalk.render('gitalk-container');
    }, 1);
  };
  useEffect(getComment, []);
  const { data } = props;
  const newData = {
    ...data,
    post: {
      ...data.post,
      date: new Date(data.post.date).toLocaleDateString('vi-VN', options),
    },
  };

  return (
    <>
      <BasedPost {...props} data={newData} />
      <div
        style={{
          maxWidth: 672,
          padding: '0 30px',
          margin: '0 auto',
        }}
        className="form-container"
      >
        <div>
          <h2>ng k nhn bi</h2>
          <p>
            Vi danh d ca mt lp trnh vin chn chnh, ha s khng gi mail
            xm.
          </p>
        </div>
        <form
          action="https://send.pageclip.co/AXgiDVy9PvCHGhETXCPi1o5psKfZODK5/contact-form"
          className="pageclip-form"
          method="post"
        >
          <label htmlFor="name">Nn gi bn l...?</label>
          <input
            className="form__input"
            type="text"
            name="name"
            id="name"
            required
          />
          <label htmlFor="email">Mnh xin mail ca bn nh!</label>
          <input
            className="form__input"
            type="email"
            name="email"
            id="email"
            required
          />

          <button type="submit" className="pageclip-form__submit">
            <span>Gi</span>
          </button>
        </form>
      </div>
      <div
        id="gitalk-container"
        style={{
          maxWidth: 672,
          padding: '0 30px',
          margin: '0 auto',
        }}
      ></div>
      <Helmet>
        <script
          src="https://s.pageclip.co/v1/pageclip.js"
          charset="utf-8"
        ></script>
      </Helmet>
    </>
  );
};

export default Post;
